<template>
  <div
    class="checkbox"
  >
    <label for="quan">
      <!--  $event vue -->
      <input
        id="quan"
        type="checkbox"
        @click="checkAll($event)"
      > 
    </label>
    <label>
      <!-- v-model  -->
      <input
        class="checkItem"
        type="checkbox"
        value="apple"
        v-model="checkData"
      > apple
    </label>
    <label>
      <input
        class="checkItem"
        type="checkbox"
        value="banana"
        v-model="checkData"
      > banana
    </label>
    <label>
      <input
        class="checkItem"
        type="checkbox"
        value="orange"
        v-model="checkData"
      > orange
    </label>
  </div>
</template>
<script>
export default {
  data() {
    return {
      checkData: [] // checkbox
    };
  },
  watch: {
    // 
    checkData: {
      handler() {
        // 
        if (this.checkData.length == 3) {
          document.querySelector("#quan").checked = true;
        } else {
          document.querySelector("#quan").checked = false;
        }
      },
      deep: true // 
    }
  },
  methods: {
    checkAll(e) {
      // 
      var checkObj = document.querySelectorAll(".checkItem"); // checkbox
      if (e.target.checked) {
        // checkbox
        for (var i = 0; i < checkObj.length; i++) {
          if (!checkObj[i].checked) {
            // checkboxpush
            this.checkData.push(checkObj[i].value);
          }
        }
      } else {
        // checkbox
        this.checkData = [];
      }
    }
  }
};
</script>



<style>
</style>



