<template>
  <div class="apply-agent-1">
    <div class="process-wrap">
      <ul class="df ac jc">
        <li
          class="f1 df ac jc"
          v-for="(item,index) in stepList"
          :key="index"
        >
          <div class="step df fdc ac jc">
            <span :class="{redbg:now===index,whitetext:now===index}">{{index+1}}</span>
            <span>{{item}}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="agent-information">
      <ul>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>

        <li class="df jsb ac">
          <span></span>
          <div>
            <span class="gray"></span>
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-arrowright"></use>
            </svg>
          </div>
        </li>
        <li class="df jsb ac">
          <span></span>
          <div>
            <span class="gray"></span>
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-arrowright"></use>
            </svg>
          </div>
        </li>
        <li class="df jsb ac">
          <span></span>
          <div>
            <span class="gray"></span>
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-arrowright"></use>
            </svg>
          </div>
        </li>
      </ul>
        <ul>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>
        <li class="df ac pr">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
          <div class="get-code pa"></div>
        </li>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>
        <li class="df ac">
          <span></span>
          <input
            class="gray"
            type="text"
            placeholder=""
          >
        </li>


      </ul>
      
    </div>
    <div class="submit"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stepList: [
        "",
        "",
        "",
        ""
      ],
      now: 0
    };
  }
};
</script>

<style lang="scss" scoped>
.process-wrap {
  @include w-h(100%, 1.6rem);
  padding: 0.25rem 0.3rem;
  box-sizing: border-box;
  background: #fff;
  ul {
    li {
      & > div {
        z-index: 100;
      }
      position: relative;
      .step {
        span {
          word-break: keep-all;
          font-size: 0.16rem;
        }
        .redbg {
          background: $red;
        }
      }
    }
    & li:nth-child(3):before,
    & li:nth-child(4)::before {
      content: "";
      @include w-h(50%, 1px);
      background: #c1c1c1;
      position: absolute;
      top: 0.23rem;
      left: 0;
    }
    & li:nth-child(2):before {
      content: "";
      @include w-h(50%, 1px);
      background: $red;
      position: absolute;
      top: 0.23rem;
      left: 0;
    }

    & li:nth-child(2):after,
    & li:nth-child(3):after{
      content: "";
      @include w-h(50%, 1px);
      background: #c1c1c1;
      position: absolute;
      top: 0.23rem;
      right: 0;
    }
    & li:nth-child(1):after {
      content: "";
      @include w-h(50%, 1px);
      background: $red;
      position: absolute;
      top: 0.23rem;
      right: 0;
    }
  }
}
.step span:first-child {
  @include w-h(0.46rem, 0.46rem);
  border: 1px solid #c1c1c1;
  background: #fff;
  border-radius: 50%;
  text-align: center;
  line-height: 0.46rem;
  margin-bottom: 0.3rem;
}
.agent-information {
  
  ul {
  margin-top: 0.13rem;
  background: #fff;
  padding: 0 0.3rem;
  box-sizing: border-box;
    li {
      height: 1rem;
      & span:first-child {
        width: 20%;
      }
      @include border-bottom;
    }
    & li:last-child{
        border-bottom: none;
    }
    
  }
}
.get-code{
    @include w-h(1.5rem,0.5rem);
    border: 1px solid $red;
    color: $red;
    text-align: center;
    line-height: 0.5rem;
    right: 0;
    border-radius: 0.04rem;
    font-size: 0.16rem;
}
.submit{
    @include w-h(6.9rem,0.8rem);
    background: $red;
    color: #fff;
    text-align: center;
    line-height: 0.8rem;
    margin: 1rem auto;
}
</style>



